import React, { useState } from "react";
import {
  Grid,
  Box,
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  TextField,
  Badge,
  borderRadius,
  styled,
  InputBase,
  Stack,
  Autocomplete,
  Avatar,
  Button,
  Menu,
  MenuItem,
  ButtonGroup,
  Checkbox,
  Skeleton,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ButtonBase,
  Divider,
} from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import DensityMediumIcon from "@mui/icons-material/DensityMedium";
import MailIcon from "@mui/icons-material/Mail";
import WbSunnyIcon from "@mui/icons-material/WbSunny";
import ShareIcon from "@mui/icons-material/Share";
import NightlightIcon from "@mui/icons-material/Nightlight";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import AssignmentIndIcon from "@mui/icons-material/AssignmentInd";
import LogoutIcon from "@mui/icons-material/Logout";

import HomeIcon from "@mui/icons-material/Home";
import ArticleIcon from "@mui/icons-material/Article";
import PeopleIcon from "@mui/icons-material/People";
import StorefrontIcon from "@mui/icons-material/Storefront";
import PersonAddIcon from "@mui/icons-material/PersonAdd";
import SettingsIcon from "@mui/icons-material/Settings";
import AccountBoxIcon from "@mui/icons-material/AccountBox";
import { Favorite, FavoriteBorder } from "@mui/icons-material";
import ShoppingBagOutlinedIcon from "@mui/icons-material/ShoppingBagOutlined";
import SearchOutlinedIcon from "@mui/icons-material/SearchOutlined";
import { Link } from "react-router-dom";
import axios from "axios";
import Cookies from "universal-cookie";
import IconDarkMode from "./IconDarkMode";
const StyleToolbar = styled(Toolbar)({
  display: "flex",
  justifyContent: "space-between",
});
const Search = styled("div")(({ theme }) => ({
  backgroundColor: "white",
  padding: "0 10px",
  borderRadius: "20px 20px  ",
  width: "40%",
}));
const Icoonnave = styled(Box)(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  gap: "20px",
}));

function Navetow(props) {
  const Cookiees = new Cookies();
  const token = Cookiees.get("Bearer");
  const label = { inputProps: { "aria-label": "Checkbox demo" } };
  const [open, setopen] = useState(false);
  const [openNve, setopenNve] = useState(false);

  return (
    <>
      <div>
        <Drawer open={openNve} onClose={() => setopenNve(false)}>
          <Box>
            <List>
              <ListItem>
                <ListItemButton component={Link} to="/">
                  <IconButton>
                    <HomeIcon />
                  </IconButton>
                </ListItemButton>
              </ListItem>
              <ListItem>
                <ListItemButton component={Link} to="/">
                  <IconButton>
                    <ArticleIcon />
                  </IconButton>
                </ListItemButton>
              </ListItem>
              <ListItem>
                <ListItemButton>
                  <IconButton>
                    <PeopleIcon />
                  </IconButton>
                </ListItemButton>
              </ListItem>
              <ListItem>
                <ListItemButton>
                  <IconButton>
                    <StorefrontIcon />
                  </IconButton>
                </ListItemButton>
              </ListItem>
              <ListItem>
                <ListItemButton>
                  <IconButton>
                    <PersonAddIcon />
                  </IconButton>
                </ListItemButton>
              </ListItem>
              <ListItem>
                <ListItemButton>
                  <IconButton>
                    <SettingsIcon />
                  </IconButton>
                </ListItemButton>
              </ListItem>
              <ListItem>
                <ListItemButton>
                  <IconButton>
                    <AccountBoxIcon />
                  </IconButton>
                </ListItemButton>
              </ListItem>
            </List>
          </Box>
        </Drawer>
        <StyleToolbar
          sx={{ borderTop: "0.1px solid #f8f6f450" }}
          className="container"
        >
          <Typography
            sx={{
              display: { xs: "none", md: "block" },
            }}
            variant="h6"
          >
            MOSTFA SHOPING<span style={{ color: "yellow" }}>BAG</span>
          </Typography>
          <Box>
            <DensityMediumIcon
              onClick={() => setopenNve(true)}
              sx={{
                display: { xs: "flex", md: "none" },
                color: "white",
              }}
            />
          </Box>
          <Search sx={{ display: "flex", alignItems: "center" }}>
            <SearchOutlinedIcon color="secondary" />
            <SearchIcon color="primary" sx={{ display: { xs: "none" } }} />
            <InputBase placeholder="searh ..." />
          </Search>
          <Icoonnave>
            <Badge badgeContent={3} color={"error"}>
              <ShoppingBagOutlinedIcon />
            </Badge>

            <Checkbox
              {...label}
              icon={<FavoriteBorder color="info" />}
              checkedIcon={<Favorite color="inherit" />}
            />
            <IconDarkMode />
          </Icoonnave>
        </StyleToolbar>
      </div>
    </>
  );
}

export default Navetow;
