import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Container,
  Dialog,
  Grid,
  Rating,
  Stack,
  Typography,
} from "@mui/material";
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import ToggleButton from "@mui/material/ToggleButton";
import imag11 from "./imgCard/2.jpg";
import imag222 from "./imgCard/1.jpg";
import { Close } from "@mui/icons-material";
import { AddShoppingCartOutlined } from "@mui/icons-material";

import { useContext } from "react";
import { ColorModeContext, getDesignTokens } from "./them";
import { IconButton, useTheme } from "@mui/material";
import "./CardHome.css";
import { imagees } from "./../../../Api/Image";
import Hero from "./Hero";
import IconSection from "./IconSection";
import { DarkModeOutlined, LightModeOutlined } from "@mui/icons-material";
import Brightness4Icon from "@mui/icons-material/Brightness4";
import Brightness7Icon from "@mui/icons-material/Brightness7";
import ProductDetails from "./ProductDetails";

function CardHome() {
  const [value, setValue] = React.useState();

  const colorMode = useContext(ColorModeContext);

  const theme = useTheme();
  console.log(theme.palette.mode);
  const mod = getDesignTokens();
  console.log(mod.palette);
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <Container>
      <Grid container>
        {imagees.map((imag, index) => {
          return (
            <Grid
              key={index}
              className="CardHomeeall"
              item
              my={4}
              lg={4}
              md={6}
              sm={12}
            >
              <Card className="CardHomee" sx={{ ml: 3, maxWidth: { lg: 345 } }}>
                <CardMedia
                  sx={{
                    height: 350,
                    width: 300,
                    margin: "auto",
                  }}
                  image={imag}
                />
                <CardContent>
                  <Stack
                    bgcolor={theme.palette.myColor}
                    direction={"row"}
                    alignItems={"center"}
                    justifyContent={"space-between"}
                  >
                    <Typography
                      sx={{ fontWeight: "bold", textTransform: "capitalize" }}
                      gutterBottom
                      variant="h5"
                      component="div"
                    >
                      lizard
                    </Typography>
                    <Stack direction={"row"} alignItems={"center"}>
                      <Typography
                        variant="h6"
                        mr={1}
                        sx={{ fontWeight: "bold", color: "#D32F2F" }}
                      >
                        $70
                      </Typography>
                      <Typography variant="h8" sx={{ color: "#a39c9c" }}>
                        <del>$100</del>
                      </Typography>
                    </Stack>
                  </Stack>
                  <Typography variant="body2" color="text.secondary">
                    Lizards are a widespread group of squamate reptiles, with
                    over 6,000 species, ranging across all continents except
                    Antarctica
                  </Typography>
                  <Box>
                    <Stack
                      direction={"row"}
                      justifyContent={"space-between"}
                      alignItems={"center"}
                      my={1}
                      spacing={1}
                    >
                      <Button
                        onClick={() => handleClickOpen()}
                        variant="contained"
                        size="small"
                      >
                        Add to Card
                      </Button>
                      <Rating
                        name="half-rating"
                        defaultValue={4.5}
                        precision={0.5}
                        size="large"
                      />
                    </Stack>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          );
        })}
      </Grid>
      <Dialog
        sx={{ ".MuiPaper-root": { minWidth: { xs: "100%", md: 800 } } }}
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <IconButton
          sx={{
            ":hover": { color: "red", rotate: "180deg", transition: "0.3s" },
            position: "absolute",
            top: 0,
            right: 10,
          }}
          onClick={handleClose}
        >
          <Close />
        </IconButton>
        {/* ProductDetails >>>> */}
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            gap: 2.5,
            flexDirection: { xs: "column", sm: "row" },
          }}
        >
          <Box sx={{ display: "flex" }}>
            <img width={360} src={imag11} alt="" />
          </Box>

          <Box sx={{ py: 2, textAlign: { xs: "center", sm: "left" } }}>
            <Typography variant="h5">mostfa safi</Typography>
            <Typography
              my={0.4}
              fontSize={"22px"}
              color={"crimson"}
              variant="h6"
            >
              $23
            </Typography>
            <Typography variant="body1">
              Reference site about Lorem Ipsum, giving information on its
              origins, as well as a random Lipsum generator.
            </Typography>

            <Stack
              sx={{ justifyContent: { xs: "center", sm: "left" } }}
              direction={"row"}
              gap={1}
              my={2}
            >
              <ToggleButtonGroup
                exclusive
                sx={{
                  ".Mui-selected": {
                    border: "1px solid royalblue !important",
                    borderRadius: "5px !important",
                    opacity: "1",
                    backgroundColor: "initial",
                  },
                }}
              >
                {[imag11, imag222].map((item_mostfa) => {
                  return (
                    <ToggleButton
                      sx={{
                        width: "110px",
                        height: "110px",
                        mx: 1,
                        p: "0",
                        opacity: "1",
                      }}
                    >
                      <img
                        style={{ borderRadius: 3 }}
                        height={"100%"}
                        width={"100%"}
                        src={item_mostfa}
                        alt=""
                      />
                    </ToggleButton>
                  );
                })}
              </ToggleButtonGroup>
            </Stack>

            <Button
              sx={{ mb: { xs: 1, sm: 0 }, textTransform: "capitalize" }}
              variant="contained"
            >
              <AddShoppingCartOutlined sx={{ mr: 1 }} fontSize="small" />
              Buy now
            </Button>
          </Box>
        </Box>
      </Dialog>
    </Container>
  );
}

export default CardHome;
